import { mount } from 'enzyme';
import React from 'react';
import { Icon } from './Icon';
import { defaultValues } from '../../constants/defaultValues';
import { describe, it } from 'storybook-addon-specifications';
import expect from 'expect';

export const Component = (name, isRotating) => {
  return <Icon name={name} isRotating={isRotating} />;
};

let output;

export const tests = (
  icon = Component(
    defaultValues.isDisabled,
    defaultValues.isFullWidth,
    defaultValues.isRound,
    defaultValues.size,
    defaultValues.type,
    defaultValues.children
  )
) => {
  output = mount(icon);

  return describe('Button', () => {
    describe('Icon', () => {
      const output = mount(icon);

      it(`Should have name: ${name}`, () =>
        expect(output.prop('name')).toEqual(name));

      it(`Should have isRotating: ${isRotating}`, () =>
        expect(output.prop('isRotating')).toEqual(isRotating));
    });
  });
};

tests();
